<template>
  <div class="clock-container">
    <div class="clock" v-if="analogMode" @click="switchMode()">
      <div class="clock-frame">
        <div class="clock-center"></div>
        <ul class="digits">
          <li class="slice">
            <div class="slice-content"><a>1</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>2</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>3</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>4</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>5</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>6</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>7</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>8</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>9</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>10</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>11</a></div>
          </li>
          <li class="slice">
            <div class="slice-content"><a>12</a></div>
          </li>
        </ul>
        <div>
          <div class="clk-hourhand" v-bind:style="hourStyle"></div>
          <div class="clk-minutehand" v-bind:style="minuteStyle"></div>
          <div class="clk-secondhand" v-bind:style="secondStyle"></div>
        </div>
      </div>
    </div>
    <div class="clock-numeric" v-if="!analogMode" @click="switchMode()">
      <span class="hour">{{ hour }}</span> :
      <span class="minute">{{ minute }}</span> :
      <span class="second">{{ second }}</span>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Clock',
  data () {
    return {
      analogMode: true,
      hourStyle: {},
      minuteStyle: {},
      secondStyle: {},
      hour: 0,
      minute: 0,
      second: 0
    }
  },
  mounted () {
    this.update()
    setInterval(this.update, 1000)
  },
  methods: {
    switchMode () {
      this.analogMode = !this.analogMode
    },
    update () {
      const time = new Date()

      this.hour = time.getHours()
      this.minute = time.getMinutes()
      this.second = time.getSeconds()

      if (this.hour > 12) {
        this.hour -= 12
      }

      this.hourStyle = {
        transform: 'rotate(' + (this.hour * 30 + (this.minute / 2) + 180) + 'deg)'
      }
      this.minuteStyle = {
        transform: 'rotate(' + (this.minute * 6 + 180) + 'deg)'
      }
      this.secondStyle = {
        transform: 'rotate(' + (this.second * 6 + 180) + 'deg)'
      }
    }
  }
}
</script>

